<template>
  <div>
    <template>
      <el-tag>主题回调模板</el-tag>
      <el-row style="float: right">
        <el-button type="primary" @click="dialogFormVisible=true" size="middle">新建模板</el-button>
      </el-row>
      <el-dialog title="新建模板" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="topic名称" :label-width="formLabelWidth">
            <template>
              <el-select v-model="form.topic_name" placeholder="请选择" filterable clearable allow-create>
                <el-option
                  v-for="item in options"
                  :key="item.name"
                  :label="item.name"
                  :value="item.name">
                </el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item label="回调http地址" :label-width="formLabelWidth">
            <el-input v-model="form.callback_url" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="createTopicCallbackTemplate">确 定</el-button>
        </div>
      </el-dialog>
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="id"
          label="模板id"
          width="120">
        </el-table-column>
        <el-table-column
          prop="topic"
          label="Topic名称"
          width="360">
        </el-table-column>
        <el-table-column
          prop="callback_url"
          label="回调地址"
          width="360">
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
          <el-button
            type="danger"
            size="mini"
            @click="NewConsumer(scope.row)">新开消费者
          </el-button>
        </template>
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <el-button
              type="danger"
              size="mini"
              @click="GetConsumerList(scope.row)">查看消费者列表
            </el-button>
          </template>
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <el-button
              type="danger"
              size="mini"
              @click="DeleteTemplate(scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog title="新建消费者协程" :visible.sync="dialogConsumerVisible">
        <el-form :model="formTwo">
          <el-form-item label="消费者组" :label-width="formLabelWidth">
            <template>
              <el-select v-model="formTwo.consumer_group" placeholder="请选择" filterable clearable allow-create>
                <el-option
                  v-for="item in consumeGroupsOptions"
                  :key="item.name"
                  :label="item.name"
                  :value="item.name">
                </el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item label="topic名称" :label-width="formLabelWidth">
            <el-input v-model="formTwo.topic_name" autocomplete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="回调http地址" :label-width="formLabelWidth">
            <el-input v-model="formTwo.callback_url" autocomplete="off" disabled></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogConsumerVisible = false">取 消</el-button>
          <el-button type="primary" @click="startConsumer">确 定</el-button>
        </div>
      </el-dialog>

      <el-dialog title="消费者列表" :visible.sync="dialogConsumerListVisible">
        <el-table :data="consumerList">
          <el-table-column property="consumer_id" label="consumer_id" width="300"></el-table-column>
          <el-table-column property="consumer_group" label="consumer_group" width="200"></el-table-column>
        </el-table>
      </el-dialog>

    </template>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [],
      dialogFormVisible: false,
      dialogConsumerVisible: false,
      dialogConsumerListVisible: false,
      form: {
        topic_name: '',
        callback_url: ''
      },
      formTwo: {
        consumer_group: '',
        topic_callback_id: '',
        topic_name: '',
        callback_url: ''
      },
      options: [],
      consumerList: [],
      consumeGroupsOptions: [],
      formLabelWidth: '120px'
    }
  },
  methods: {
    loadConsumeTemplates () {
      var that = this
      this.$http.get(this.GLOBAL.httpAddress + '/api/getConsumerTemplates').then(function (res) {
        if (res.data.code === 0) {
          that.tableData = res.data.data
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
      this.$http.get(this.GLOBAL.httpAddress + '/api/getTopics').then(function (res) {
        if (res.data.code === 0) {
          that.options = res.data.data
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
      this.$http.get(this.GLOBAL.httpAddress + '/api/getConsumerGroups').then(function (res) {
        if (res.data.code === 0) {
          that.consumeGroupsOptions = res.data.data
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
    },
    createTopicCallbackTemplate () {
      this.dialogFormVisible = false
      var that = this
      this.$http.post(this.GLOBAL.httpAddress + '/api/createConsumerTemplate', that.form, {emulateJSON: true}).then(function (res) {
        if (res.data.code === 0) {
          alert('成功')
          this.loadConsumeTemplates()
        } else {
          alert(res.data.message)
        }
      }, function (res) {
      })
    },
    NewConsumer (row) {
      this.formTwo.topic_callback_id = row.id
      this.formTwo.callback_url = row.callback_url
      this.formTwo.topic_name = row.topic
      this.dialogConsumerVisible = true
    },
    GetConsumerList (row) {
      var that = this
      this.$http.get(this.GLOBAL.httpAddress + '/api/getAllConsumersOfTemplate?template_id=' + row.id).then(function (res) {
        if (res.data.code === 0) {
          that.consumerList = res.data.data
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
      that.dialogConsumerListVisible = true
    },
    DeleteTemplate (row) {
      var that = this
      this.$http.get(this.GLOBAL.httpAddress + '/api/deleteTemplate?template_id=' + row.id).then(function (res) {
        if (res.data.code === 0) {
          alert('成功删除')
          that.loadConsumeTemplates()
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
    },
    startConsumer () {
      this.dialogConsumerVisible = false
      var that = this
      this.$http.post(this.GLOBAL.httpAddress + '/api/startConsumer', that.formTwo, {emulateJSON: true}).then(function (res) {
        if (res.body.code === 0) {
          alert('成功')
        } else {
          alert(res.body.message)
        }
      }, function (res) {
      })
      that.formTwo.consumer_group = ''
    }
  },

  mounted () {
    this.loadConsumeTemplates()
  }
}
</script>

<style>

</style>



