<template>
  <div>
    <template>
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="name"
          label="ConsumeGroup名称"
          width="1000px">
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: []
    }
  },
  methods: {
    loadTopics () {
      var that = this
      this.$http.get(this.GLOBAL.httpAddress + '/api/getConsumerGroups').then(function (res) {
        if (res.data.code === 0) {
          that.tableData = res.data.data
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
    }
  },
  mounted () {
    this.loadTopics()
  }
}
</script>

<style>

</style>



