<template>
  <div>
    <template>
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="ConsumerId"
          label="ConsumerId"
          width="280px">
        </el-table-column>
        <el-table-column label="状态" width="50px">
          <template slot-scope="scope">
            <el-row>
              <el-button type="success" icon="el-icon-check" circle v-if="scope.row.Running"></el-button>
              <el-button type="danger" icon="el-icon-check" circle v-else></el-button>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column
          prop="Topic"
          label="Topic名称"
          width="240px">
        </el-table-column>
        <el-table-column
          prop="ConsumerGroup"
          label="ConsumerGroup"
          width="300px">
        </el-table-column>
        <el-table-column
          prop="Callback"
          label="Callback地址"
          width="240px">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="closeConsumer(scope.row.ConsumerId)">关闭并删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      tableData: [],
      form: {
        consumer_id: ''
      }
    }
  },
  methods: {
    loadTopics () {
      var that = this
      this.$http.get(this.GLOBAL.httpAddress + '/api/getConsumers').then(function (res) {
        if (res.data.code === 0) {
          that.tableData = res.data.data
        } else {
          alert(res.data.message)
        }
      }, function () {
        console.log('请求失败处理')
      })
    },
    closeConsumer (consumerId) {
      this.form.consumer_id = consumerId
      var that = this
      this.$http.post(this.GLOBAL.httpAddress + '/api/closeConsumer', that.form, {emulateJSON: true}).then(function (res) {
        if (res.data.code === 0) {
          alert('成功')
          this.loadTopics()
        } else {
          alert(res.data.message)
        }
      }, function (res) {
      })
      that.form.consumer_id = ''
    }
  },
  mounted () {
    this.loadTopics()
  }
}
</script>

<style>

</style>



