import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import ConsumeGroup from '@/components/ConsumeGroup'
import ConsumeTemplate from '@/components/ConsumeTemplate'
import Consumer from '@/components/Consumer'

// 用到了路由
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/consumeGroup',
      name: 'ConsumeGroup',
      component: ConsumeGroup
    },
    {
      path: '/consumeTemplate',
      name: 'ConsumeTemplate',
      component: ConsumeTemplate
    },
    {
      path: '/consumer',
      name: 'Consumer',
      component: Consumer
    }
  ]
})



